import React from 'react'
import watchesData from '../data/watchesData'
import SingleCard from './SingleCard'
const CardList = ({filterPrice,inStock,outStock}) => {
    return (
        <>
            {
                watchesData.map(item => {
                        if(filterPrice>=item.price){
                            if(inStock===true){
                                if(item.inStock===true){
                                    return (
                                        <SingleCard
                                            allData={item}
                                            key={item.id}
                                            img={item.img}
                                            title={item.title}
                                            price={item.price}
                                            id={item.id}
                                        />
                                    )

                                }
                            }

                            if(outStock===true){
                                if(item.inStock===false){
                                return (
                                    <SingleCard
                                        allData={item}
                                        key={item.id}
                                        img={item.img}
                                        title={item.title}
                                        price={item.price}
                                        id={item.id}
                                    />
                                )
                                }
                            }
    
                            

                    }
                })
            }
        </>
    )
}

export default CardList