import React, { useEffect, useState } from 'react'
import { Accordion } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap';
import CardList from '../card/CardList';
import watchesData from '../data/watchesData'


const Shop = () => {
  //Finding in stock element
  var inStock = 0;
  var outStock = 0;
  const [defaultSearch,setDefaultSearch] = useState("");
  const [inStockState, setInStockState] = useState(true);
  const [outStockState, setOutStockState] = useState(true);
  watchesData.map((item) => {
    if (item.inStock === true) {
      inStock++;
    } else {
      outStock++;
    }
  })
  // Finding Maximum price in data  __Start__
  var highestPrice = watchesData[0].price;
  watchesData.map((item) => {
    if (item.price > highestPrice) {
      highestPrice = item.price;
    }
  })
  // Finding Maximum price in data  __End__

  // Finding Minimum price in data  __Start__
  var lowestPrice = watchesData[0].price;
  watchesData.map((item) => {
    if (item.price < lowestPrice) {
      lowestPrice = item.price;
    }
  })
  // Finding Minimum price in data  __End__


  //State

  const [watch, setWatch] = useState(0);
  const [rangeInput, setRangeInput] = useState(highestPrice);
  const [prevButtonSitutation, setPrevButtonSitutation] = useState("");
  const [nextButtonSitutation, setNextButtonSitutation] = useState("");

  useEffect(() => {
    if (watch === 0) {
      setPrevButtonSitutation("buttonEnd");
    } else {
      setPrevButtonSitutation("");
    }
    if (watch === watchesData.length - 1) {
      setNextButtonSitutation("buttonEnd");
    } else {
      setNextButtonSitutation("");
    }
  });
  return (
    <>
      <section className='shop'>
        <div className="backOfHeader">
          <div className="text text-center">
            <h3>Collection</h3>
            <p>
              <a href="/home">Home</a> / Pretty Products - Home 2</p>
          </div>
        </div>

        <div className="shopBody container-fluid">
          <div className="row">
            <div className="col-12 col-sm-12 col-md-4" id='leftMain'>
              <div className="accardionsMain">
                <Accordion defaultActiveKey="3">
                  <Accordion.Item eventKey="0">
                    <Accordion.Header> <p>Category</p> </Accordion.Header>
                    <Accordion.Body>
                      <ul>

                        <LinkContainer to="/search">
                          <li><a href="/" onClick={
                            ()=>{
                              setDefaultSearch("Smart Watches")
                            }
                          }>Smart Watches</a></li>
                        </LinkContainer>

                        <LinkContainer to="/search">
                          <li><a href="/" onClick={
                            ()=>{
                              setDefaultSearch("Digital Watches")
                            }
                          }>Digital Watches</a></li>
                        </LinkContainer>

                        <LinkContainer to="/search">
                          <li><a href="/" onClick={
                            ()=>{
                              setDefaultSearch("Analog Watches")
                            }
                          }>Analog Watches</a></li>
                        </LinkContainer>

                      </ul>
                    </Accordion.Body>
                  </Accordion.Item>
                  <hr />
                  <Accordion.Item eventKey="1">
                    <Accordion.Header> <p>Availability</p> </Accordion.Header>
                    <Accordion.Body>
                      <div className="main d-block d-sm-flex">
                        <div className="inputItem">
                          <input className='myCheckBox' type="checkbox" name="" id="checkIn" checked={inStockState}
                            onChange={
                              (e) => {
                                setInStockState(e.target.checked)
                              }
                            }
                          />
                          <p>In Stock({inStock})</p>
                        </div>

                        <div className="inputItem">
                          <input className='myCheckBox' type="checkbox" name="" id="checkOut" checked={outStockState}
                            onChange={
                              (e) => {
                                setOutStockState(e.target.checked)
                              }
                            } />
                          <p>Out of Stock({outStock})</p>
                        </div>
                      </div>
                    </Accordion.Body>
                  </Accordion.Item>

                  <hr />
                  <Accordion.Item eventKey="2">
                    <Accordion.Header> <p>Price</p> </Accordion.Header>
                    <Accordion.Body>
                      <div className="main d-block">
                        <p>The highest price is ${rangeInput}</p>
                        <input type="range" name="" id="rangeSlider" min={lowestPrice} max={highestPrice} value={rangeInput}
                          onInput={() => {
                            const rangeSlider = document.getElementById('rangeSlider')
                            setRangeInput(rangeSlider.value)
                          }}
                        />
                      </div>
                    </Accordion.Body>
                  </Accordion.Item>

                  <hr />
                  <Accordion.Item eventKey="3">
                    <Accordion.Header> <p>New Arrivals</p> </Accordion.Header>
                    <Accordion.Body>
                      <LinkContainer to={`/shop/${watchesData[watch].id}`}>
                        <div className="myCard">
                          <div className="imageFrame">
                            <img src={watchesData[watch].img} alt="" />
                            <div className="hoverContainer">
                              <i className="fa-regular fa-heart"></i>
                              <i className="fa-solid fa-bag-shopping"></i>
                            </div>
                          </div>

                          <div className="text text-center">
                            <p className='stars'>
                              <i className="fa-solid fa-star"></i>
                              <i className="fa-solid fa-star"></i>
                              <i className="fa-solid fa-star"></i>
                              <i className="fa-solid fa-star"></i>
                              <i className="fa-solid fa-star"></i>
                            </p>

                            <p className='title'>{watchesData[watch].title}</p>
                            <p className='price'>${watchesData[watch].price}</p>
                          </div>
                        </div>
                      </LinkContainer>

                      <div className="controlButtons">
                        <button id='previousButton' className={prevButtonSitutation} onClick={
                          () => {
                            if (watch !== 0) {
                              setWatch(watch - 1);
                            }
                          }
                        }><i className="fa-solid fa-chevron-left"></i></button>
                        <button className={nextButtonSitutation} onClick={
                          () => {
                            if (watch !== watchesData.length - 1) {
                              setWatch(watch + 1);
                            }
                          }
                        }><i className="fa-solid fa-chevron-right"></i></button>
                      </div>
                    </Accordion.Body>
                  </Accordion.Item>
                </Accordion>
                <hr className='my-0' />
              </div>

              <div className="bestSellers">
                <div className="head">
                  <p>Best Sellers</p>
                </div>
                <hr />

                <div className="bestSellersBody">
                  {
                    watchesData.slice(1, 4).map((item) => {
                      return (
                        <>
                          <LinkContainer to={`/shop/${item.id}`}>
                            <div className="product">
                              <div className="row">
                                <div className="col-12 col-sm-4 col-md-4">
                                  <img src={item.img} alt="" />
                                </div>

                                <div className="col-12 col-sm-4 col-md-8">
                                  <div className="text">
                                    <p className='star'><i className="fa-solid fa-star"></i><i className="fa-solid fa-star"></i><i className="fa-solid fa-star"></i><i className="fa-solid fa-star"></i><i className="fa-solid fa-star"></i></p>
                                    <p className='title'>{item.title}</p>
                                    <p className='price'>${item.price}</p>
                                  </div>
                                </div>
                              </div>
                            </div>
                          </LinkContainer>
                        </>
                      )
                    })
                  }
                  {/* <div className="product">
                  <div className="row">
                    <div className="col-4 col-sm-4 col-md-4">
                      <img src={watchesData[0].img} alt="" />
                    </div>

                    <div className="col-4 col-sm-4 col-md-8">
                      <div className="text">
                        <p className='star'><i className="fa-solid fa-star"></i><i className="fa-solid fa-star"></i><i className="fa-solid fa-star"></i><i className="fa-solid fa-star"></i><i className="fa-solid fa-star"></i></p>
                        <p className='title'>{watchesData[0].title}</p>
                        <p className='price'>${watchesData[0].price}</p>
                      </div>
                    </div>
                  </div>
                </div> */}


                </div>
              </div>
            </div>

            <div className="col-12 col-sm-12 col-md-8">
              <div className="row g-4">
                <CardList filterPrice={rangeInput} inStock={inStockState} outStock={outStockState} />
              </div>
            </div>
          </div>
        </div>
      </section>

    </>
  )
}

export default Shop